body {
  background-color: #37393d;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Global Header Styles */
.global-header {
  background-color: #37393d;
  color: white;
  padding: 1em 2em;
}

.global-header__nav {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  text-align: left;
}

.global-header li {
  display: inline;
}

.global-header__nav a {
  color: white;
}

/* Button Styles */
.button {
  padding: 1em 0.5em;
  text-decoration: none;
}

.button:hover {
  background-color: #242526;
}

/* Hero Styles */
.hero {
  background-size: cover;
  padding: 20% 0;
}

.hero__title {
  color: white;
}

/* Squares Styles */
.square {
  background-position: center;
  background-size: cover;
  padding: 7.5%;
  position: relative;
  width: 25%;
}

.square__info {
  display: none;
}

.square:hover .square__info {
  display: block;
  color: white;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  z-index: 2;
}

.square:hover::after {
  background-color: #37393d;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  opacity: 0.8;
  top: 0;
  width: 100%;
  z-index: 1;
}

/* Grid Styles */
.grid {
  display: -ms-flexbox;
  display: flex;
}

.grid__title {
  color: white;
}



